.nopost {
    margin: auto;
    width: auto;
    text-align: center;
    justify-content: center;
    border: 4px solid white;
    border-radius: 10px;
    max-width: 500px;
    margin-top: 2vh;
    color: white;
}

@media (max-width : 480px) {
    .nopost {
        width: 90%;
    }
}

